import ApiClient from "../apiClient.js";
import * as constant from "../../common/constant.js";
////////////////////////
/////// Monitoring API ///////
////////////////////////

const API_NAME = "/sse/monitoring";
//const API_NAME = "/sse/monitoring";


const apiClient = function () {
    return ApiClient.instance;
};

function init(url, timeout, isDebug) {
    return new ApiClient(url, timeout, isDebug);
}

function getSubscriptions(id) {
    return apiClient().getEventsource(id);
}


/**
 * Fetches a list of datasets.
 *
 * @param params Additional request parameters (required).
 * @returns {Promise<AxiosResponse<any>>} A promise that resolves to an object containing the response code and data.
 */
export async function getDatasets(params) {

    return apiClient()
        .axios.get(`/${params?.tntId}/dataset`)
        .then(filterKey => {
            return filterKey;
        });
}

/**
 * Fetches a dataset.
 *
 * @param dataset The dataset identifier (required).
 * @param params Additional request parameters.
 * @returns {Promise<AxiosResponse<any>>} A promise that resolves to an object containing the response code and data.
 */
export async function getDataset(dataset, params) {

    return apiClient()
        .axios.get(`/${params?.tntId}/dataset/${dataset}`)
        .then(filterKey => {
            return filterKey;
        });
}

function fetchFilterKey(token, params) {
    const baseURL = apiClient().axios.defaults.baseURL;

    return apiClient()
        .axios.post(`/${params?.tntId}/filterValue`, params)
        .then(filterKey => {
            return filterKey;
        });
}



/**
 * Fetches event source data for a given dataset.
 *
 * @param {string} dataset The dataset for which event source data is requested (required).
 * @param {Object} params Additional request parameters (required).
 * @returns {Promise<{code: string, data: string}>} A promise that resolves to an object containing the response code and data.
 */
export async function getEventSource(dataset, params) {

    let __eventSource = {
        code: ""
        ,data: ""
    };

    const filterKeyResponse = await fetchFilterKey(dataset, params);
    if (filterKeyResponse.code !== "0" ) {
        __eventSource.code = filterKeyResponse.code;
        __eventSource.data = filterKeyResponse;
        return __eventSource;
    }

    const baseURL = apiClient().axios.defaults.baseURL;
    const sseURL = `${baseURL}/${params?.tntId}/${dataset}?filterKey=${filterKeyResponse.data}${apiClient().token ? `&bcloudToken=${apiClient().token}` : ""}`;

    console.log("monitoringSdk getEventSource baseURL : ", baseURL);
    console.log("monitoringSdk getEventSource url : " , sseURL);
    console.log("monitoringSdk getEventSource apiClient() : ", apiClient());

    const eventsource = new EventSource(`${sseURL}`);
    __eventSource.code = "0";
    __eventSource.data = eventsource;
    return __eventSource;
}

